export function connecting(dispatch){
  dispatch({type: "loadingMessage", payload: ['Connecting...', '...', '...']});
}

export function connectionError(dispatch){
  dispatch({type: "message", payload: ['Connection error, Please try again!', '!', '!']});
}

export function updateSuccess(dispatch){
  dispatch({type: "message", payload: ['Update succeed!', '!', '!']});
}

export function updateFailed(dispatch){
  dispatch({type: "message", payload: ['Update failed! Please try again!', '! !', '! !']});
}



